<template>
  <div class="quanprogress" :class="{'quan-version2':version2}" @click="goExchange">
    <p class="p1" @click.stop="openActivityRule">活动规则</p>
    <p class="p2"  id="couponList"><span>{{activity_bounty.vouchers.length}}</span></p>
    <div>
      <img @click.prevent="" :src="img1" alt="" class="imgBg">
      <div class="progress-out">
        <div class="progress-in" :style="styleLong"></div>
      </div>
      <img v-if="activity_bounty.voucher_batch.value <= task_now.recharge_bounty && activity_bounty.voucher_batch.value !== 0"
           class="ling" @click.stop="goReceive" src="http://res.catchme.com.cn/activity/jiewu3/ling.png" alt="">
      <div class="right" v-else="">
        还差<span>{{Math.ceil((activity_bounty.voucher_batch.value - task_now.recharge_bounty)/info.coin_num)}}</span>次
      </div>
    </div>
  </div>
</template>

<script>
  import api from './../api/index'
  import CONFIG from './../config/index'
  import {mapState} from 'vuex'

  export default {
    name: 'quanprogress',
    data() {
      return {
        styleLong: '',
        isFirst: true,
        img1: 'http://res.catchme.com.cn/activity/jiewu2/bg_short2.png',
        img2: 'http://res.catchme.com.cn/activity/jiewu2/bg_long2.png',
        img3: 'http://res.catchme.com.cn/activity/jiewu3/bg_ling.png',
        img4: 'http://res.catchme.com.cn/activity/jiewu3/bg_ling_long.png',
        needle: 0
      }
    },
    props: ['cinemaInfo'],
    computed: {
      ...mapState({
        activity_promocode: state => state.user.activity_promocode,
        activity_bounty: state => state.user.activity_bounty,
        task_now: state => state.user.task_now,
        info: state => state.user.info,
//        isLogin: state => state.user.isLogin
      })
    },
    mounted() {
      if (this.version2) {
        this.img1 = this.img2;
      }
//      if (this.isLogin) {
//        this.mountedStart();
//      }
    },
    methods: {
//      mountedStart() {
//        this.$store.dispatch('getActivityBounty').then((res) => {
//          if (res.bounty < res.voucher.value) {
//            var bili = res.bounty / res.voucher.value;
//            this.styleLong = 'width:' + bili * 100 + '%';
//          } else {
//            this.styleLong = 'width:100%';
//            if (this.version2) {
//              this.img1 = this.img4;
//            } else {
//              this.img1 = this.img3;
//            }
//          }
//        })
//      },
      goExchange() {
        if (this.activity_bounty.vouchers && this.activity_bounty.vouchers.length === 0) {
          this.$emit('openTip', 'notExchange');
          _hmt.push(['_trackEvent','打开优惠券列表弹窗', '点击', '优惠券为：0', '']);
        } else {
//          this.$emit('openTip', 'exchange');
          _hmt.push(['_trackEvent','打开优惠券列表弹窗', '点击', '优惠券为：'+this.activity_bounty.voucher_batch.name, '']);
          this.$emit('openTip', 'couponList');
        }
      },
      openActivityRule() {
        this.$emit('openTip', 'activityRule');
      },
      goReceive() {
        this.$emit('openTip', 'receive');
      }
    },
    watch: {
//      activity_bounty(newActivity, oldActivity) {
//        if (this.isFirst) {
//          this.isFirst = false;
//          return;
//        }
//        //如果充值成功之后获取到用户的进度没有改变，再过3秒重新请求
//        if (newActivity.bounty === oldActivity.bounty) {
//          console.log('数据未改变发送请求')
//          setTimeout(() => {
//            this.$store.dispatch('getActivityBounty');
//          }, 3000);
//          return;
//        }
//        if (newActivity.bounty < newActivity.voucher.value) {
//          var bili = newActivity.bounty / newActivity.voucher.value;
//          this.styleLong = 'width:' + bili * 100 + '%';
//        } else {
//          this.styleLong = 'width:100%';
//          if (this.version2) {
//            this.img1 = this.img4;
//          } else {
//            this.img1 = this.img3;
//          }
//        }
//        if (newActivity.voucher.value <= newActivity.bounty) {
//          this.goReceive();
//        }
//      },
//      isLogin() {
//        this.mountedStart();
//      }
      activity_bounty() {
        //只有第一次才会根据来请求奖励金设置进度，后面奖励金的改变，都是根据task_now的监听实现的
        if (!this.isFirst) {
          return;
        }
        this.isFirst = false;
        this.$store.dispatch('getActivityBountyInfo').then((res) => {
          if (res.recharge_bounty < this.activity_bounty.voucher_batch.value) {
            var bili = res.recharge_bounty / this.activity_bounty.voucher_batch.value;
            this.styleLong = 'width:' + bili * 100 + '%';
          } else {
            this.styleLong = 'width:100%';
            if (this.version2) {
              this.img1 = this.img4;
            } else {
              this.img1 = this.img3;
            }
          }
        });
      },

      task_now(newActivity,oldActivity){
        var task = localStorage.getItem('task');
        console.log('11111111111')
        if(task){
          localStorage.removeItem('task');
          return;
        }
        console.log('2222222')
        //这里需要判断是不是充值之后改变的
//        if(newActivity.game_bounty !== oldActivity.game_bounty || newActivity.prize_bounty !== oldActivity.prize_bounty){
//          return;
//        }
        if(newActivity.prize_bounty !== oldActivity.prize_bounty){
          return;
        }
        console.log('333333')
        if(newActivity.recharge_bounty<this.activity_bounty.voucher_batch.value){
          var bili = newActivity.recharge_bounty/this.activity_bounty.voucher_batch.value;
          this.styleLong = 'width:' + bili * 100 + '%';
        }else {
          this.styleLong = 'width:100%';
          if (this.version2) {
            this.img1 = this.img4;
          } else {
            this.img1 = this.img3;
          }
        }
        console.log('newActivity--------'+newActivity.recharge_bounty);
        console.log('oldActivity-----------'+oldActivity.recharge_bounty);
        console.log(newActivity.recharge_bounty === oldActivity.recharge_bounty);
        if(newActivity.recharge_bounty === oldActivity.recharge_bounty){
          console.log('数据未改变发送请求')
          setTimeout(()=>{
            this.$store.dispatch('getActivityBountyInfo');
          },3000);
          return;
        }
        if(this.activity_bounty.voucher_batch.value <= newActivity.recharge_bounty ){
          this.goReceive();
        }
      }

    }
  }
</script>

<style lang="scss" scoped>
  .quanprogress {
    width: 516px;
    /*width: 526px;*/
    position: absolute;
    /*padding: 0 22px;*/
    /*float: right;*/
    right: 20px;
    /*margin: 0 22px 0 0;*/
    bottom: 0;
    > div {
      position: relative;
      .imgBg {
        width: 100%;
        display: block;
      }
      .right {
        padding: 0 10px;
        height: 48px;
        background: url("./../assets/jiewu2/haicbg.png") no-repeat;
        background-size: 100% 100%;
        position: absolute;
        right: 14px;
        bottom: 14px;
        font-size: 20px;
        color: #39f9ff;
        display: flex;
        align-items: center;
        justify-content: center;
        text-shadow: 0 0 5px #f8449f;
        font-weight: 600;
        span {
          font-size: 28px;
        }
      }
    }
    .p1 {
      width: 240px;
      position: absolute;
      padding: 10px 10px;
      /*line-height: 30px;*/
      display: flex;
      align-items: center;
      text-align: center;
      font-size: 20px;
      background: rgba(35, 8, 85, 0.68);
      color: #fff;
      left: 10px;
      top: 10px;
      border-radius: 10px;
      opacity: 0;
      z-index: 666;
    }
    .p2 {
      width: 200px;
      height: 42px;
      position: absolute;
      right: 22px;
      top: 18px;
      z-index: 2;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      font-size: 20px;
      padding: 8px 0 0 0;
      span {
        display: block;
        width: 36px;
        text-align: center;
        margin: 4px 10px 0 0;
        &.long {
          font-size: 26px;
        }
      }
    }
  }

  .ling {
    width: 114px;
    height: 116px;
    position: absolute;
    right: -16px;
    bottom: -20px;
  }

  .progress-out {
    width: 430px;
    height: 28px;
    /*background: #491b8f;*/
    background: url("./../assets/jiewu3/progress_bg.png") no-repeat;
    background-size: 100% 100%;
    /*border: 3px solid red;*/
    border-radius: 19px;
    overflow: hidden;
    position: absolute;
    left: 34px;
    bottom: 24px;
    padding: 0;
  }

  .progress-in {
    width: 0;
    height: 28px;
    border-radius: 0 19px 19px 0;
    /*background: linear-gradient(to right,#6a2de2,#fb419d);*/
    border-right: 4px solid #39f9ff;
    background: url("http://res.catchme.com.cn/activity/jiewu3/progress.png") no-repeat;
    transition: all 0.5s;
    background-size: auto 100%;
  }

  .quan-version2 {
    width: 492px;
    /*width: 509px;*/
    .progress-out {
      /*width: 380px;*/
      bottom: 26px;
      left: 30px;
    }
    .p1 {
      font-size: 22px;
    }
    .p2 {
      /*height: 24px;*/
      /*top:20px;*/
      padding: 10px 0 0 0;
      top: 16px;
      span {
        font-size: 26px;
        margin: 0 10px 0 0;
      }
    }
    > div {
      .right {
        font-size: 22px;
        right: 16px;
        bottom: 16px;
        span {
          font-size: 32px;
        }
      }
    }

  }

</style>



